<template>
  <div class="basicInfo">
    <div class="Btitle">
      <router-link class="basicData" :to="{name:'accountInfo'}">账户总额</router-link>
      <span class="basicDat2">></span>
      <router-link class="basicData1" :to="{name:'incomeAmount'}" tag="span">收入明细</router-link>
    </div>
    <div class="contact1">
      <div class="subTitle">
        收入明细
        <span class="firstLevel" @click="goBack">返回首级</span>
      </div>
    </div>
    <div class="abilityOverview" style="width:83.8vw;height:24.3vh;">
      <IEcharts :options="abilityOverview" @click="onClick" style="width:83.8vw;height:24.3vh"></IEcharts>
    </div>
    <div class="contact2">
      <div class="runVIPTable" style="width:83.8vw;height:49vh;">
        <table>
          <thead>
            <tr class="firstChild">
              <th v-for="(text,i) in tableThTextArr" :key="i">{{text}}</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item,i) in bableArr" :key="i">
              <td>{{item.number}}</td>
              <td>{{item.name}}</td>
              <td class="contactTd1">{{item.money}}</td>
              <td>{{item.state}}</td>
              <td>{{item.way_type}}</td>
              <td>{{item.create_time}}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <paginate
      :page-count="pageQuantity"
      :margin-pages="1"
      :force-page="forcePage"
      :page-range="10"
      page-class="page-item"
      page-link-class="item"
      container-class="ui pagination menu"
      prev-text="<"
      next-text=">"
      prev-link-class="item"
      next-link-class="item"
      break-view-class="break-view"
      break-view-link-class="break-view-link"
      :click-handler="clickCallback"
      :no-li-surround="true"
    >
      <span slot="breakViewContent" @click="jumpPage">
        <svg width="16" height="4" viewBox="0 0 16 4">
          <circle fill="#999999" cx="2" cy="2" r="2" />
          <circle fill="#999999" cx="8" cy="2" r="2" />
          <circle fill="#999999" cx="14" cy="2" r="2" />
        </svg>
      </span>
    </paginate>
  </div>
</template>

<script>
import Paginate from 'vuejs-paginate';
import IEcharts from 'vue-echarts';
import 'echarts/lib/component/tooltip';
import { orderBoard } from '../mixin/orderBoard';
import 'echarts/lib/chart/line';
export default {
	name: 'incomeAmount',
	mixins: [orderBoard],
	components: { IEcharts, Paginate },
	data() {
		return {
			tableThTextArr: [
				'订单编号',
				'订单名称',
				'充值金额',
				'充值状态',
				'支付方式',
				'充值时间'
			],
			// 订单列表默认参数为无
			bableArr: []
		};
	},
	methods: {
		// echarts下钻事件
		onClick(e) {
			if (e.name.indexOf('年') !== -1) {
				this.echartsClick(
					'收入订单',
					'MONTH',
					e.name.substr(0, e.name.length - 1) ,
					'api/accountOverview',
					2
				);
			} else if (e.name.indexOf('月') !== -1) {
				let event = e.name.substr(0, e.name.length - 1);
				if (event.length === 1) {
					event = '2019-0' + event;
				} else if (event.length === 2) {
					event = '2019-' + event;
				}
				this.echartsClick('收入订单', 'DAY', event, 'api/accountOverview', 3);
			} else if (e.name.indexOf('日') !== -1) {
				this.init('api/accountOverview', '收入订单');
			}
		},
		clickCallback(val) {
			this.pagesCallback(val, 'api/accountOverview', '收入订单');
		},
		goBack(){
			this.init('api/accountOverview', '收入订单');
		}
	},
	mounted() {
		this.init('api/accountOverview', '收入订单');
	}
};
</script>

