<template>
  <div class="basicInfo">
    <div class="Btitle">
      <span class="basicData">订单管理</span>
    </div>
    <div class="contact1">
      <div class="subTitle">
        订单趋势
        <span class="upperLevel"
          v-if="false">上一级</span>
        <span class="firstLevel"
          @click="postFn(2019, 1, consumeTypeSortBl, 'api/trendRecord')">返回首级</span>
      </div>
      <div class="runVIPEcharts"
        style="width:83.8vw;height:24.3vh;">
        <IEcharts :options="abilityOverview"
          @click="onClick"
          style="width:83.8vw;height:24.3vh"></IEcharts>
      </div>
    </div>
    <div class="contact2">
      <div class="subTitle">
        <span :class="{consumeTypeSort:true,bgc:consumeTypeSortBl==3 }"
          @click="orderTypeClick(3)">充值列表</span>
        <span :class="{consumeTypeSort:true,bgc:consumeTypeSortBl==2 }"
          @click="orderTypeClick(2)">消费列表</span>
        <span :class="{consumeTypeSort:true,bgc:consumeTypeSortBl==1 }"
          @click="orderTypeClick(1)">收入清单</span>
        <span class="order"
          @click.stop='dropDownIconBl=!dropDownIconBl'
          v-click-outside="{fn:closIsDrop,arg:{inquire:'dropDownIconBl'}}">
          <i class="icon biFont icon-order"></i>
          <div class="dropDownIcon"
            v-show="dropDownIconBl">
            <span @click="
				pagesCallback(
					1,
					url,
					prefix,
					filterIndex,
					requestType,
					0
				)">
              <span class="icon biFont icon-descend"></span>
              <span>升序</span>
            </span>
            <span @click="
				pagesCallback(
					1,
					url,
					prefix,
					filterIndex,
					requestType,
					1
				)">
              <span class="icon biFont icon-ascend"></span>
              <span>降序</span>
            </span>
          </div>
        </span>
        <span class="break">
          <span class="icon biFont icon-reset"></span>
        </span>
        <span class="search">
          <input type="text"
            v-model="searchVal"
            @blur="
				pagesCallback(
					1,
					url,
					prefix,
					searchVal,
					'模糊搜索',
					0
				)"
            @keyup.enter="
				pagesCallback(
					1,
					url,
					prefix,
					searchVal,
					'模糊搜索',
					0
				)">
          <span class="icon userCenterFont icon-search"
            @click="
				pagesCallback(
					1,
					url,
					prefix,
					searchVal,
					'模糊搜索',
					0
				)"></span>
        </span>
        <span class="searchText">搜索：</span>
      </div>
      <div class="runVIPTable"
        style="width:83.8vw;height:49vh;">
        <table>
          <thead>
            <tr>
              <th class="userAppellation">用户名称</th>
              <th class="userPhone">手机号码</th>
              <th class="orderNum">订单号</th>
              <th class="orderType">订单状态
                <span v-if="!dropDownObj.orderType">
                  <img src="@/assets/UserCenter/imgs/triangleDown.svg"
                    alt=""
                    @click.stop="dropDownFn('orderType')"
                    v-if="!dropDownObj.mouseleaveorderType"
                    @mouseenter="dropDownObj.mouseleaveorderType=true"
                    class="triangle">
                  <img src="@/assets/UserCenter/imgs/triangleDown-blue.svg"
                    alt=""
                    @click.stop="dropDownFn('orderType')"
                    v-if="dropDownObj.mouseleaveorderType"
                    @mouseleave="dropDownObj.mouseleaveorderType=false"
                    class="triangle">
                </span>
                <span v-if="dropDownObj.orderType">
                  <img src="@/assets/UserCenter/imgs/triangleUp.svg"
                    alt=""
                    @click.stop="dropDownObj.orderType=false"
                    v-if="!dropDownObj.mouseleaveorderType"
                    @mouseenter="dropDownObj.mouseleaveorderType=true"
                    class="triangle">
                  <img src="@/assets/UserCenter/imgs/triangleUp-blue.svg"
                    alt=""
                    @click.stop="dropDownObj.orderType=false"
                    v-if="dropDownObj.mouseleaveorderType"
                    @mouseleave="dropDownObj.mouseleaveorderType=false"
                    class="triangle">
                </span>
                <div class="dropDown"
                  v-click-outside="{fn:closIsDrop,arg:{msg:'orderType'}}"
                  v-if="dropDownObj.orderType">
                  <span v-for="(item,i) in orderType"
                    :key="i"
                    @click="pagesCallback(1,url,prefix,i,'订单状态',0)">{{item}}</span>
                </div>
              </th>
              <th class="payType">支付类型
                <span v-if="!dropDownObj.payType">
                  <img src="@/assets/UserCenter/imgs/triangleDown.svg"
                    alt=""
                    @click.stop="dropDownFn('payType')"
                    v-if="!dropDownObj.mouseleavepayType"
                    @mouseenter="dropDownObj.mouseleavepayType=true"
                    class="triangle">
                  <img src="@/assets/UserCenter/imgs/triangleDown-blue.svg"
                    alt=""
                    @click.stop="dropDownFn('payType')"
                    v-if="dropDownObj.mouseleavepayType"
                    @mouseleave="dropDownObj.mouseleavepayType=false"
                    class="triangle">
                </span>
                <span v-if="dropDownObj.payType">
                  <img src="@/assets/UserCenter/imgs/triangleUp.svg"
                    alt=""
                    @click.stop="dropDownObj.payType=false"
                    v-if="!dropDownObj.mouseleavepayType"
                    @mouseenter="dropDownObj.mouseleavepayType=true"
                    class="triangle">
                  <img src="@/assets/UserCenter/imgs/triangleUp-blue.svg"
                    alt=""
                    @click.stop="dropDownObj.payType=false"
                    v-if="dropDownObj.mouseleavepayType"
                    @mouseleave="dropDownObj.mouseleavepayType=false"
                    class="triangle">
                </span>
                <div class="dropDown"
                  v-click-outside="{fn:closIsDrop,arg:{msg:'payType'}}"
                  v-if="dropDownObj.payType">
                  <span v-for="(item,i) in payType"
                    :key="i"
                    @click="pagesCallback(1,url,prefix,i,`支付类型`,0)">{{item}}</span>
                </div>
              </th>
              <th class="parMoney">支付金额</th>
              <th class="firstBuy">是否首次购买
                <span v-if="!dropDownObj.firstBuy">
                  <img src="@/assets/UserCenter/imgs/triangleDown.svg"
                    alt=""
                    @click.stop="dropDownFn('firstBuy')"
                    v-if="!dropDownObj.mouseleavefirstBuy"
                    @mouseenter="dropDownObj.mouseleavefirstBuy=true"
                    class="triangle">
                  <img src="@/assets/UserCenter/imgs/triangleDown-blue.svg"
                    alt=""
                    @click.stop="dropDownFn('firstBuy')"
                    v-if="dropDownObj.mouseleavefirstBuy"
                    @mouseleave="dropDownObj.mouseleavefirstBuy=false"
                    class="triangle">
                </span>
                <span v-if="dropDownObj.firstBuy">
                  <img src="@/assets/UserCenter/imgs/triangleUp.svg"
                    alt=""
                    @click.stop="dropDownObj.firstBuy=false"
                    v-if="!dropDownObj.mouseleavefirstBuy"
                    @mouseenter="dropDownObj.mouseleavefirstBuy=true"
                    class="triangle">
                  <img src="@/assets/UserCenter/imgs/triangleUp-blue.svg"
                    alt=""
                    @click.stop="dropDownObj.firstBuy=false"
                    v-if="dropDownObj.mouseleavefirstBuy"
                    @mouseleave="dropDownObj.mouseleavefirstBuy=false"
                    class="triangle">
                </span>
                <div class="dropDown"
                  v-click-outside="{fn:closIsDrop,arg:{msg:'firstBuy'}}"
                  v-if="dropDownObj.firstBuy">
                  <span v-for="(item,i) in isMaidenType"
                    :key="i"
                    @click="pagesCallback(1,url,prefix,i,`是否首次购买`,0)">{{item}}</span>
                </div>
              </th>
              <th class="modify">操作</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(arr,i) in tbodyArr"
              :key="i">
              <td class="userAppellation">{{arr.nickname}}</td>
              <td>{{arr.phone}}</td>
              <td>{{arr.topup_number}}</td>
              <td>{{arr.topup_state}}</td>
              <td>{{arr.topup_way}}</td>
              <td>{{arr.topup_money}}</td>
              <td>{{arr.isBuy}}</td>
              <td>
                <span class="icon biFont icon-edit"></span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <paginate :page-count="pageQuantity"
      :margin-pages="1"
      :force-page="forcePage"
      :page-range="10"
      :page-class="'page-item'"
      :page-link-class="'item'"
      :container-class="'ui pagination menu'"
      :prev-text="'<'"
      :next-text="'>'"
      :prev-link-class="'item'"
      :next-link-class="'item'"
      :break-view-class="'break-view'"
      :break-view-link-class="'break-view-link'"
      :click-handler="clickCallback"
      :no-li-surround="true">
      <span slot="breakViewContent"
        @click="jumpPage">
        <svg width="16"
          height="4"
          viewBox="0 0 16 4">
          <circle fill="#999999"
            cx="2"
            cy="2"
            r="2" />
          <circle fill="#999999"
            cx="8"
            cy="2"
            r="2" />
          <circle fill="#999999"
            cx="14"
            cy="2"
            r="2" />
        </svg>
      </span>
    </paginate>
  </div>
</template>

<script>
import Paginate from 'vuejs-paginate';
import IEcharts from 'vue-echarts';
export default {
	name: 'runOrder',
	components: { IEcharts, Paginate },
	data() {
		return {
			searchVal: '',
			orderType: ['无'],
			payType: ['无'],
			isMaidenType: ['无'],
			// 总页数
			pageQuantity: 1,
			page: 0,
			forcePage: 1,
			// 页码点击事件控制,防止频繁点击
			pageClickBl: true,
			dropDownIconBl: false,
			// echarts数据表，初始化各种参数为0
			abilityOverview: {
				tooltip: {},
				xAxis: {
					data: [],
					show: true,
					axisLabel: {
						color: '#fff',
						fontSize: '12'
					}
				},
				grid: {
					x: 60,
					x2: 40,
					y: 30,
					y2: 30
				},
				visualMap: {
					show: false,
					min: 1,
					max: 31,
					dimension: 0,
					inRange: {
						color: ['#1d7cfa', '#00f8ff', '#f86e81']
					}
				},
				yAxis: {
					type: 'value',
					show: true,
					inverse: false,
					axisLabel: {
						verticalAlign: 'middle',
						color: '#fff',
						fontSize: '12'
					},
					splitLine: {
						show: true,
						lineStyle: {
							color: '#2d3540'
						}
					},
					axisLine: {
						show: true
					},
					axisTick: {
						show: true
					}
				},
				series: [
					{
						name: '',
						type: 'pictorialBar',
						symbol: 'rect',
						barWidth: '30',
						symbolMargin: '50%',
						itemStyle: {
							normal: {
								barBorderRadius: 5 //柱状角成椭圆形
							}
						},
						symbolRepeat: true,
						symbolSize: ['70%', '15%'],
						data: []
					}
				]
			},
			dropDownObj: {
				orderType: false,
				payType: false,
				firstBuy: false,
				mouseleaveorderType: false,
				mouseleavepayType: false,
				mouseleavefirstBuy: false
			},
			tbodyArr: [
				{
					nickname: '小明',
					phone: '18888888888',
					topup_number: '9527',
					topup_state: '已支付',
					topup_money: '999',
					topup_type: '支付宝',
					topup_way: '否',
					isBuy: '是'
				}
			],
			consumeTypeSortBl: 3,
			prefix: '',
			url: '',
			requestType: '所有',
			filterIndex: 0,
			dataSort: 0
		};
	},
	methods: {
		// echarts下钻事件
		onClick(event) {
			this.echartsClick(event, 'api/trendRecord', this.consumeTypeSortBl);
		},
		// echarts点击事件分类，有查询月和查询日之分
		echartsClick(event, url, getType) {
			if (event.name.indexOf('年') >= 0) {
				this.postFn(event.name, 1, getType, url);
			} else if (event.name.indexOf('月') >= 0) {
				this.postFn(event.name, 2, getType, url);
			}
		},
		// 下钻事件，post请求函数
		postFn(time, dataType, getType, url) {
			this.$post(url, {
				type: getType,
				date: parseInt(time),
				dateType: dataType,
				isMember: 2
			}).then(res => {
				if (res.code == '000000') {
					if (res.data) {
						var arr = [];
						var arr1 = [];
						if (dataType == 1) {
							for (const i in res.data) {
								if (i != 0 && res.data[i]) {
									arr.push(res.data[i].money);
									arr1.push(res.data[i].monthly + '月');
								}
							}
						} else if (dataType == 2) {
							for (const i in res.data) {
								if (i != 0) {
									if (res.data[i]) {
										arr.push(res.data[i].money);
										arr1.push(parseInt(i) + '日');
									} else {
										arr.push(0);
										arr1.push(parseInt(i) + '日');
									}
								}
							}
						}
						this.abilityOverview.xAxis.data = arr1;
						this.abilityOverview.series[0].data = arr;
					} else {
						this.$store.dispatch('publicTools/setTipsData', [
							'年暂时没有充值数据'
						]);
					}
				}
			});
		},
		// 页码点击事件
		clickCallback(val) {
			if (this.requestType == '所有') {
				this.pagesCallback(
					val,
					this.url,
					this.prefix,
					0,
					'所有',
					this.dataSort
				);
			} else {
				this.pagesCallback(
					val,
					this.url,
					this.prefix,
					this.filterIndex,
					this.requestType,
					this.dataSort
				);
			}
		},
		// 页码上十页，下十页的封装
		jumpPage(obj) {
			if (this.pageClickBl) {
				if (obj.x < obj.y) {
					if (this.forcePage - 10 > 0) {
						this.clickCallback(this.forcePage - 10);
					} else {
						this.clickCallback(1);
					}
				} else {
					if (this.forcePage + 10 < this.pageQuantity) {
						this.clickCallback(this.forcePage + 10);
					} else {
						this.clickCallback(this.pageQuantity);
					}
				}
			} else {
				return;
			}
		},
		// get请求的封装--不同接口返回的字段有一些差异
		pagesCallback(val, url, prefix, index, type, sort) {
			if (this.pageClickBl) {
				var urlVal = `${val}&value=${index - 1}&condition=${type}&sort=${sort}`;
				if (index == 0 && type != '模糊搜索') {
					urlVal = `${val}&sort=${sort}`;
				}
				if (type == '模糊搜索') {
					urlVal = `${val}&value=${index}&condition=${type}&sort=${sort}`;
				}
				this.pageClickBl = false;
				this.$get(url + urlVal).then(res => {
					this.pageClickBl = true;
					if (res.code == '000000') {
						var data = res.data[`${prefix}order_data`];
						this.forcePage = val;
						this.dataSort = sort;
						// 假设是筛选的，把筛选条件保存起来
						if (type != '所有') {
							this.requestType = type;
							this.filterIndex = index;
						} else {
							this.requestType = '所有';
						}
						var arr = [];
						if (data) {
							for (let i = 0; i < data.length; i++) {
								if (!data[i]['isBuy']) {
									data[i]['isBuy'] = '无';
								}
								arr.push({
									nickname: data[i].nickname,
									phone: data[i].phone,
									topup_number: data[i][`${prefix}number`],
									topup_state: data[i][`${prefix}state`],
									topup_type: data[i][`${prefix}type`],
									topup_money: data[i][`${prefix}money`],
									topup_way: data[i][`${prefix}way`],
									isBuy: data[i]['isBuy']
								});
							}
						} else {
							arr.push({
								nickname: '无',
								phone: '无',
								topup_number: '无',
								topup_type: '无',
								topup_state: '无',
								topup_money: '无',
								isBuy: '无',
								topup_way: '无'
							});
						}

						// 赋值总页数
						if (res.data.count) {
							this.pageQuantity = res.data.count;
						} else {
							this.pageQuantity = 1;
						}
						// 赋值table
						this.tbodyArr = arr;
						// 隐藏所有下拉按钮
						for (const key in this.dropDownObj) {
							this.dropDownObj[key] = false;
						}
						// 趋势图
						// var xAxisData = [];
						// var seriesData = [];
						// if (res.data.annuaMoney) {
						// 	for (let i = 0; i < res.data.annuaMoney.length; i++) {
						// 		xAxisData.push(res.data.annuaMoney[i].annual + '年');
						// 		seriesData.push(res.data.annuaMoney[i].money);
						// 	}
						// }
						// this.abilityOverview.xAxis.data = xAxisData;
						// this.abilityOverview.series[0].data = seriesData;
					}
				});
			}
		},
		// 切换三种订单类型点击事件
		orderTypeClick(index) {
			this.consumeTypeSortBl = index;
			if (index == 3) {
				this.prefix = 'topup_';
				this.url = 'api/topupOrderAdmin?page=';
			} else if (index == 2) {
				this.prefix = 'consume_';
				this.url = 'api/consumeOrderAdmin?page=';
			} else if (index == 1) {
				this.prefix = 'income_';
				this.url = 'api/incomeOrderAdmin?page=';
			}
			this.pagesCallback(1, this.url, this.prefix, 0, '所有', 0);
			this.postFn(2019, 1, this.consumeTypeSortBl, 'api/trendRecord');
		},
		// 下拉框排他事件
		closIsDrop(arg) {
			if (arg.inquire) {
				this[arg.inquire] = false;
				return;
			}
			this.dropDownObj[arg.msg] = false;
		},
		// 下拉框显示点击事件---增加了阻止冒泡
		dropDownFn(blKey) {
			for (const key in this.dropDownObj) {
				if (key == blKey) {
					this.dropDownObj[key] = true;
				} else {
					this.dropDownObj[key] = false;
				}
			}
		}
	},
	mounted() {
		this.orderTypeClick(3);
		this.postFn(2019, 1, this.consumeTypeSortBl, 'api/trendRecord');
		this.$get('api/sizerCondition').then(res => {
			if (res.code == '000000') {
				res.data.orderType.unshift('全部');
				res.data.payType.unshift('全部');
				res.data.isMaidenType.unshift('全部');
				this.orderType = res.data.orderType;
				this.payType = res.data.payType;
				this.isMaidenType = res.data.isMaidenType;
			}
		});
	}
};
</script>

<style scoped>
.basicInfo >>> .pagination {
  position: fixed;
  bottom: 2vh;
  font-size: 0.8vw;
  width: 83.8vw;
  text-align: center;
}
.basicInfo >>> .item {
  display: inline-block;
  font-size: 0.8vw;
  width: 1.6vw;
  height: 1.6vw;
  text-align: center;
  line-height: 1.6vw;
  border: solid 0.0625vw #00b9ff;
  margin-left: 0.5vw;
}
.basicInfo >>> .active {
  background-color: #3dadff;
  border: solid 0.0625vw #3dadff;
}

.basicInfo >>> .ignore {
  display: inline;
  font-size: 0.8vw;
}
.basicInfo .Btitle {
  height: 4.5vh;
  line-height: 3vh;
  padding-top: 0.05vh;
}
.basicInfo .Btitle .basicData {
  border-left: solid #28b3fc 0.0625vw;
  padding-left: 0.5vw;
  font-size: 0.9vw;
  margin-right: 3vw;
}
.basicInfo .subTitle {
  font-size: 0.9vw;
  padding: 1.3vh 0 1.3vh 1.3vw;
  border-bottom: solid 0.0625vw #0e121a;
}
.basicInfo .contact1 {
  background-color: #1c2433;
  width: 83.8vw;
  margin-top: 1.5vh;
}
.basicInfo .contact2 {
  background-color: #1c2433;
  width: 83.8vw;
  height: 46vh;
  margin-top: 1.5vh;
}
.firstLevel {
  float: right;
  font-size: 0.7vw;
  margin-right: 0.6vw;
  padding: 0.6vh 0.6vw;
  background-color: #283247;
  border: 0.0625vw solid #283247;
  position: relative;
  top: -0.2vw;
  cursor: pointer;
  line-height: 0.7vw;
}
.upperLevel {
  float: right;
  font-size: 0.7vw;
  margin-right: 1.5vw;
  padding: 0.6vh 0.6vw;
  line-height: 0.7vw;
  background-color: #283247;
  border: 0.0625vw solid #283247;
  position: relative;
  top: -0.2vw;
  cursor: pointer;
}
.firstLevel:hover {
  background-color: #1c3d57;
  border: solid 0.0625vw #28b3fc;
}
.upperLevel:hover {
  background-color: #1c3d57;
  border: solid 0.0625vw #28b3fc;
}
.order {
  float: right;
  margin-right: 1.5vw;
  font-size: 0.8vw;
  margin-top: 0.1vh;
  position: relative;
}
.break {
  float: right;
  font-size: 0.8vw;
  margin-right: 0.6vw;
  margin-top: 0.4vh;
  line-height: 1vw;
}
.search {
  float: right;
  font-size: 0.8vw;
  margin-right: 0.6vw;
  margin-top: 0.1vh;
  position: relative;
  color: #acbbda;
}
.search input {
  width: 15.3vw;
  height: 2.4vh;
  background-color: #11151f;
